.mapLogo img{
    /* position: relative; */
    z-index: 10;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-name: slidein;
            animation-name: slidein;
}
@-webkit-keyframes slidein {
    from {
        opacity: 0;
    }
  
    to {
        opacity: 1;
    }
}
@keyframes slidein {
    from {
        opacity: 0;
    }
  
    to {
        opacity: 1;
    }
}

